import React, {PureComponent} from 'react'
import {
  Table,
  message,
  Icon,
  Popover,
  Modal,
  Row,
  Col,
  Button
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import JsBarcode from 'jsbarcode'
import {BBC} from 'Mock'
const OUT = ['24', '25', '55']  // S07，S08，S11的波次

@connect(
  state => ({...state.outWarehouseListWave, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.isAllot = false
    this.columns = [{
      title: '操作',
      width: 250,
      dataIndex: 'defaultStorageLocationFlag',
      key: 'defaultStorageLocationFlag',
      render: (text, record, index) => <div className="tableOperation">
        {/* <span className={(OUT.indexOf(record.businessType) != -1 && ((record.allDistributeFlag && record.status == 2) || (!record.allDistributeFlag && record.status == 1))) ? null : 'noClick'} onClick={() => {
          if (!(OUT.indexOf(record.businessType) != -1 && ((record.allDistributeFlag && record.status == 2) || (!record.allDistributeFlag && record.status == 1)))) return
          const {handleExportList} = this.props
          handleExportList({searchParams: {waveId: record.id}}, () => {
            message.success('导出成功')
          })
        }}>导出</span> */}
                <span className={(record.status == 3 || record.status == 0 || record.status == 9) ? 'noClick' : null} onClick={() => {
                  if (record.status == 3 || record.status == 0 || record.status == 9) return
                  const {handleOut, handleShippedcheck, update} = this.props
          // 增加BBC出库的逻辑
                  Modal.confirm({
                    title: '提示',
                    content: '是否确认出库？',
                    okText: '确认',
                    cancelText: '取消',
                    onOk: () => {
                      if (BBC.indexOf(record.businessType) != -1) {
                        handleShippedcheck({data: {waveCode: record.orderCode}}, ({result}) => {
                          switch (result.result) {
                            case 1 : handleOut({urlParams: [record.id]}, result => {
                              const {handleGetList, searchParams, page, size, tableActiveKey, update} = this.props
                              handleGetList({searchParams: {page, size, ...searchParams}}, '', tableActiveKey)
                              message.info(`${result.message}`)
                              if (result.isShortage) {
                                if (record.businessType == 18) {
                                  update({isShowReportModal: true, modalId: record.orderCode, modalData: record, reportModalType: 1})
                                }
                              }
                            }, (data) => {
                              const {update} = this.props
                              if (data.code == 30001085 && record.businessType == 18) {
                                update({isShowReportModal: true, modalId: record.orderCode, modalData: record, reportModalType: 1})
                              }
                            }); break
                            case 0 : update({BBCModalData: record, isShowBBCModal: true, BBCmessage: result.message});break
                            case -1 : message.error(result.message);break
                            default: break
                          }
                        })
                      } else {
                        handleOut({urlParams: [record.id]}, result => {
                          const {handleGetList, searchParams, page, size, tableActiveKey, update} = this.props
                          handleGetList({searchParams: {page, size, ...searchParams}}, '', tableActiveKey)
                          message.info(`${result.message}`)
                          if (result.isShortage) {
                            if (record.businessType == 18) {
                              update({isShowReportModal: true, modalId: record.orderCode, modalData: record, reportModalType: 1})
                            }
                          }
                        }, (data) => {
                          const {update} = this.props
                          if (data.code == 30001085 && record.businessType == 18) {
                            update({isShowReportModal: true, modalId: record.orderCode, modalData: record, reportModalType: 1})
                          }
                        })
                      }
                    }
                  })
                }}>出库</span>
        <span onClick={() => {
          const {handleExportList} = this.props
          handleExportList({searchParams: {waveId: record.id}}, () => {
            message.success('导出成功')
          })
        }}>导出</span>
        <span className={(record.allDistributeFlag || record.status == 3) ? 'noClick' : null} onClick={() => {
          if (record.allDistributeFlag || this.isAllot || record.status == 3) return
          const {handleAllot} = this.props
          message.loading('库存分配中请耐心等待')
          this.isAllot = true
          handleAllot({data: {waveId: record.id}}, () => {
            message.destroy()
            message.success('分配成功')
            this.isAllot = false
            const {handleGetList, searchParams, page, size, tableActiveKey} = this.props
            handleGetList({searchParams: {page, size, ...searchParams}}, '', tableActiveKey)
          }, () => {
            this.isAllot = false
          })
        }}>分配库存</span>

        <span className={(!record.status) ? 'noClick' : null} onClick={() => {
          if (!record.status) return
          this.onEdit('isShowStockoutModal', record)
        }}>缺货明细</span>
        <span onClick={() => {
          this.onEdit('isShowRecordModal', record)
        }}>记录</span>
      </div>
    }, {
      title: '波次号',
      width: 150,
      dataIndex: 'orderCode',
      key: 'orderCode',
      render: (text, record, index) => <React.Fragment><a onClick={() => {
        const {history: {location}} = this.props
        window.open(`${location.pathname}/${record.id}`)
      }}>{text}</a><Popover content={<img id={`key${text}`} style={{width: '221px', height: '50px'}} />} title={text} trigger="click"><Icon type="file-text" className='my-icon' onClick={this.handlePrint.bind(this, text)} /></Popover></React.Fragment>
    },
    {
      title: '序号',
      width: 150,
      dataIndex: 'waveNum',
      key: 'waveNum'
    },
    {
      title: '业务类型',
      width: 150,
      dataIndex: 'businessTypeDesc',
      key: 'businessTypeDesc'
    }, {
      title: '状态',
      width: 100,
      dataIndex: 'statusDesc',
      key: 'statusDesc'
    }, {
      title: '预计发货日期',
      width: 150,
      dataIndex: 'planSendDate',
      key: 'planSendDate'
    }, {
      title: '订单数量',
      width: 100,
      dataIndex: 'orderNumber',
      key: 'orderNumber'
    }, {
      title: '全量分配',
      width: 100,
      dataIndex: 'allDistributeFlagDesc',
      key: 'allDistributeFlagDesc',
      render: (text, record) => <span className={record.allDistributeFlag == 1 ? null : 'red'}>{text}</span>
    }, {
      title: '门店类型',
      width: 100,
      dataIndex: 'shopTypeDesc',
      key: 'shopTypeDesc'
    }, {
      title: '波次单打印次数',
      width: 150,
      dataIndex: 'wavePrintTimes',
      key: 'wavePrintTimes'
    }, {
      title: '出库单打印次数',
      width: 150,
      dataIndex: 'outPrintTimes',
      key: 'outPrintTimes'
    }, {
      title: '订单已出库数',
      width: 100,
      dataIndex: 'outCount',
      key: 'outCount'
    }, {
      title: '订单未出库数',
      width: 100,
      dataIndex: 'unOutCount',
      key: 'unOutCount'
    }, {
      title: '创建人',
      width: 100,
      dataIndex: 'createBy',
      key: 'createBy'
    }, {
      title: '创建时间',
      width: 150,
      dataIndex: 'createTime',
      key: 'createTime'
    }, {
      title: '最新分配时间',
      width: 150,
      dataIndex: 'distributeTime',
      key: 'distributeTime'
    }]
  }
  componentDidMount() {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (!userLoation || !userLoation.value) return
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, userLoation} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({searchParams: {page: 1, size: newProps.size, ...newProps.searchParams}})
    }
  }
  // 打印条形码
  handlePrint = (text) => {
    setTimeout(() => {
      JsBarcode(`#key${text}`, text, {
        width: 1.5,
        height: 30,
        displayValue: false
      })
    })
  }
  //一些操作
  onEdit = (modalType, record) => {
    const {update} = this.props
    update({[modalType]: true, modalId: [record.orderCode], outModalData: record})
  }

  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size, tableActiveKey} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({searchParams: {page, size, status: tableActiveKey, ...searchParams, isAsc: true, columns: sorter.field}}, true)
      } else {
        handleGetList({searchParams: {page, size, status: tableActiveKey, ...searchParams, isAsc: false, columns: sorter.field}}, true)
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({searchParams: {page: pagination.current, size: pagination.pageSize, status: tableActiveKey, ...searchParams}}, true)
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult,
      selectedRowKeys,
      isShowBBCModal,
      BBCModalData,
      update,
      handleOut,
      handleWarnexport,
      BBCmessage
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const {update} = this.props
        update({selectedRowKeys, selectedRows})
      }
      // getCheckboxProps: record => ({
      //   disabled: record.status !== '待审批' // Column configuration not to be checked
      // })
    }
    return (
      <div className='table-group'>
        <Modal
          title="温馨提示"
          width="350px"
          visible={isShowBBCModal}
          footer={null}
          onOk={this.handleOk}
          onCancel={() => {
            update({isShowBBCModal: false})
          }}
        >
          <p style={{textAlign: 'center', color: 'red'}}>{BBCmessage}</p>
          <Row
            type="flex"
            justify="center"
          >
            <Col>
              <Button style={{marginRight: '48px'}} onClick={() => {
                update({isShowBBCModal: false})
                handleOut({urlParams: [BBCModalData.id]}, result => {
                  const {handleGetList, searchParams, page, size, tableActiveKey, update} = this.props
                  handleGetList({searchParams: {page, size, ...searchParams}}, '', tableActiveKey)
                  message.info(`${result.message}`)
                  if (result.isShortage) {
                    if (BBCModalData.businessType == 18) {
                      update({isShowReportModal: true, modalId: BBCModalData.orderCode, modalData: BBCModalData, reportModalType: 1})
                    }
                  }
                }, (data) => {
                  const {update} = this.props
                  if (data.code == 30001085 && BBCModalData.businessType == 18) {
                    update({isShowReportModal: true, modalId: BBCModalData.orderCode, modalData: BBCModalData, reportModalType: 1})
                  }
                })
              }}>继续出库</Button>
              <Button type="primary" onClick={() => {
                update({isShowBBCModal: false})
                handleWarnexport({data: {waveCode: BBCModalData.orderCode}}, () => {
                  message.success('操作成功')
                })
              }}>下载异常订单</Button>
            </Col>
          </Row>
        </Modal>
        <Table
          rowKey={(record, index) => index}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={pagination}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



