import React, {PureComponent} from 'react'
import {
  message,
  Button,
  Tabs,
  Modal
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {setVariant} from 'Variant'
const {TabPane} = Tabs
const confirm = Modal.confirm
import {QYG, SCTYPE} from 'Mock'
import {getNowPageId} from '../../global/components/menuData'

@connect(
  state => ({...state.outWarehouseListWave}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  componentDidMount() {
    const {GetButtonPower} = this.props
    const MEUN = JSON.parse(localStorage.getItem('b2bUserMenu'))
    const ID = getNowPageId(MEUN, window.location.pathname)
    GetButtonPower({urlParams: [ID]})
  }
  /* 打印预览 */
  handleBrowse = (type) => {
    let {selectedRows} = this.props
    if (!selectedRows.length) {
      message.error('请勾选订单')
      return
    }
    if (type == 2) {
      let typeNum = 1 // 默认wms的打印
      let pass = true
      let nowType = ''
      selectedRows = selectedRows.filter((o) => o.status == 3)
      if (!selectedRows.length) {
        message.error('只有"已发货"状态的订单才可打印')
        return
      }
      //判断是否是同一种的类型
      for (let i of Object.keys(selectedRows)) {
        if (i == 0) {
          nowType = selectedRows[i].businessType
        } else {
          if (nowType != selectedRows[i].businessType) {
            pass = false
            break
          }
        }
      }
      if (!pass) {
        message.error('请选择同一种业务类型')
        return
      }
      // 判断是否是商超的类型，需要选择是否过滤为0的商品
      new Promise((resolve, reject) => {
        if (SCTYPE.indexOf(nowType) != -1) {
          confirm({
            title: '请选择',
            content: '是否过滤出库数量0的商品？',
            cancelText: '否',
            okText: '是',
            onOk() {
              resolve(true)
            },
            onCancel() {
              resolve(false)
            }
          })
        } else {
          resolve(false)
        }
      }).then(r => {
        setVariant('wms-print-filterZero', r)
        // 判断类型是跳自己的打印还是跳企业购的打印 1: wms , 2 企业购
        QYG.indexOf(nowType) != -1 ? typeNum = 2 : typeNum = 1
        if (typeNum == 2) {
          let orderList = []
          selectedRows.map((o) => {
            orderList.push({
              waveId: o.id
            })
          })
          const {handlChangeCode} = this.props
          handlChangeCode({data: orderList}, (data) => {
            setVariant('orderList', data.result)

            const {handleUpdatePrintNum} = this.props
            handleUpdatePrintNum({data: orderList}, 2)

            window.open('/sms/outingListBatch', 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
          })
        } else {
          let arr = []
          selectedRows.map((o, i) => arr.push(o.id))
          setVariant('wms/outWarehouseListWave', arr)
          window.open(`/wms/outWarehouseListWave/Print/${type}`, 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
        }
      })
    } else {
      let arr = []
      selectedRows.map((o, i) => arr.push(o.orderCode))
      setVariant('wms/outWarehouseListWave', arr)
      window.open(`/wms/outWarehouseListWave/Print/${type}`, 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
    }
  }
  // 导出
  handleExport = () => {
    const {handleExport, searchParams, page, size} = this.props
    handleExport({searchParams: {page, size, ...searchParams}}, () => {
      message.success('导出成功')
    })
  }

  // 出库
  // handleOut = () => {
  //   const {selectedRowKeys, handleOut} = this.props
  //   if ( !selectedRowKeys.length ) {
  //     message.error('请勾选订单')
  //     return
  //   }
  //   handleOut({urlParams: selectedRowKeys}, () => {
      // message.success('操作成功')
      // const {handleGetList, searchParams, page, size} = this.props
      // handleGetList({searchParams: {page, size, ...searchParams}})
  //   })
  // }

  // 分配库存
  // handleAllot = () => {
  //   const {selectedRowKeys, handleAllot} = this.props
  //   if ( !selectedRowKeys.length ) {
  //     message.error('请勾选订单')
  //     return
  //   }
  //   handleAllot({data: selectedRowKeys.toString(), urlParams: selectedRowKeys.toString()}, () => {
  //     message.loading('库存分配中请耐心等待', 5)
  //     const {handleGetList, searchParams, page, size} = this.props
  //     handleGetList({searchParams: {page, size, ...searchParams}})
  //   })
  // }
    // 查询数据
  query = (tableActiveKey) => {
    const {handleGetList, searchParams, size, update} = this.props
    let reqObj = {...searchParams}
    update({tableActiveKey})
    console.log(tableActiveKey)
    // 在切换tab的时候，也要带着status去查询
    // handleGetList({searchParams: {...reqObj, page: 1, status: tableActiveKey, size}}, true)
    handleGetList({searchParams: {...reqObj, page: 1, status: '', size}}, false, tableActiveKey)
  }
  render() {
    const {tableActiveKey, tab0, tab1, tab2, tab3, tab9, outwarehouseWaveExportBoci, outwarehouseWaveExportChuku} = this.props
    return (
      <div className={style['table-tool']}>
        {/* <Button type="primary" className={style.btn} onClick={this.handleAllot}>分配库存</Button> */}
        {
          outwarehouseWaveExportBoci && <Button type="primary" className={style.btn} onClick={this.handleBrowse.bind(this, 1)}>打印波次单</Button>
        }
        {
          outwarehouseWaveExportChuku && <Button type="primary" className={style.btn} onClick={this.handleBrowse.bind(this, 2)}>打印出库单</Button>
        }
        {/* <Button type="primary" className={style.btn} onClick={this.handleOut}>出库</Button> */}
        <Tabs onChange={this.query} activeKey={tableActiveKey} >
          <TabPane tab={`初始化(${tab0 || 0})`} key="0"></TabPane>
          <TabPane tab={`分配中(${tab1 || 0})`} key="1"></TabPane>
          <TabPane tab={`已分配(${tab2 || 0})`} key="2"></TabPane>
          <TabPane tab={`已发货(${tab3 || 0})`} key="3"></TabPane>
          <TabPane tab={`已取消(${tab9 || 0})`} key="9"></TabPane>
        </Tabs>
      </div>
    )
  }
}



