import {Bread} from 'Bread'
import React, {PureComponent} from 'react'
import Form from './components/./Form'
import Table from './components/./Table'
import Tool from './components/./Tool'
import RecordModal from './components/./RecordModal'
import StockoutModal from './components/./StockoutModal'
import OperationModal from './components/./OperationModal'
import ReportModal from './components/./ReportModal'
import {connect} from 'react-redux'
import {mapDispatchToProps} from './redux/mapDispatchToProps'
import modal from './redux/modal'

@connect(
  state => ({...state.outWarehouseListWave}),
  mapDispatchToProps
)
export default class App extends PureComponent {
  constructor() {
    super()
  }
  componentWillUnmount() {
    const {update} = this.props
    update(modal)
  }
  render() {
    const {history} = this.props
    return (
      <section >
        <Bread route={['出库管理', '波次管理']} />
        <Form />
        <Tool />
        <Table history={history}/>
        {
          this.props.isShowModal ? <OperationModal /> : null
        }
        {
          this.props.isShowRecordModal ? <RecordModal /> : null
        }
        {
          this.props.isShowStockoutModal ? <StockoutModal /> : null
        }
        {
          this.props.isShowReportModal ? <ReportModal /> : null
        }
      </section>
    )
  }
}



