import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input,
  Checkbox,
  DatePicker
} from 'antd'
import style from './style/Form.scss'
import {API_WAVES_BUSINESSKEYVALUES, API_WAVES_SHOPTYPEKEYVALUES} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect} from 'Form'
import moment from 'moment'
const {RangePicker} = DatePicker

@connect(
  state => ({...state.outWarehouseListWave, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  componentWillMount() {
    this.initDateOpt()
  }
    // 设置默认的时间  前一月 - 当天
  initDateOpt(values) {
    const {update} = this.props
    const preMonth = moment().subtract(1, 'M').startOf('day')
    update({searchParams: {initDate: [preMonth, moment().endOf('day')]}})
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }
  // 重置
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
         // 重置时间
    this.initDateOpt()
  }

  // 新增
  handleAdd = () => {
    const {update} = this.props
    update({isShowModal: true, modalTitle: '创建波次', modalData: '', searchResultRecord: [], selectedRowKeysOM: [],
      selectedRowsOM: []})
  }
  render() {
    const {
      searchParams: {
        businessType,
        allDistributeFlag,
        // status,
        outPrintTimes,
        orderCode,
        wavePrintTimes,
        shopType,
        initDate,
        initDate2
      }
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
          gutter={16}
        >
          <Col span={6}>
            <div className='commonInput'>
              <label>订单查询：</label>
              <div className='box'>
                <Input placeholder='支持各种单号查询' value={orderCode} onChange={(e) => {
                  this.onInputChange({orderCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_WAVES_BUSINESSKEYVALUES}
              label='业务类型'
              selectItem={{data: businessType, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({businessType: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_WAVES_SHOPTYPEKEYVALUES}
              label='门店类型'
              selectItem={{data: shopType, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({shopType: value})
              }}
            />
          </Col>
          {/* <Col span={6}>
            <CsxSelect
              dateApi={API_WAVES_STATUSKEYVALUES}
              label='状态'
              selectItem={{data: status, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({status: value})
              }}
            />
          </Col> */}
          <Col span={6}>
            <CsxSelect
              label='已打印出库单'
              data={[{key: '', value: '全部'}, {key: true, value: '是'}, {key: false, value: '否'}]}
              selectItem={{data: outPrintTimes, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({outPrintTimes: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              label='已打印波次单'
              data={[{key: '', value: '全部'}, {key: true, value: '是'}, {key: false, value: '否'}]}
              selectItem={{data: wavePrintTimes, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({wavePrintTimes: value})
              }}
            />
          </Col>
          <Col span={6}>
            <Checkbox style={{marginLeft: 30}} checked={allDistributeFlag ? true : false} onChange={(e) => {
              this.onInputChange({allDistributeFlag: e.target.checked})
            }}>分配短缺</Checkbox>
          </Col>
          <Col span={12}>
            <div className='commonInput'>
              <label>创建时间：</label>
              <div className='box' style={{marginRight: 0}}>
                <RangePicker onChange={(moment, string) => {
                  this.onInputChange({initDate: moment})
                }}
                  format={'YYYY-MM-DD'}
                  value={initDate}
                  allowClear={false}
                />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <div className='commonInput'>
              <label>预计发货日期：</label>
              <div className='box' style={{marginRight: 0}}>
                <DatePicker
                  onChange={(moment, string) => {
                    this.onInputChange({initDate2: moment})
                  }}
                  format={'YYYY-MM-DD'}
                  value={initDate2}
                />
              </div>
            </div>
          </Col>
          <Col span={18}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
              <Button type="primary" className={style.mr10} onClick={this.handleAdd}>创建波次</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



