import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Input,
  Form,
  message,
  Button,
  DatePicker,
  Table
} from 'antd'
import {CsxFormSelect, CsxFormMultipleSelect} from 'Form'
import {API_WAVES_BUSINESSKEYVALUES, API_WAVES_STATUSKEYVALUES, API_WAVES_SHOPKEYVALUES, API_WAVES_SHOPTYPEKEYVALUES} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import moment from 'moment'
import {NOSENDFLAG, BBC} from 'Mock'
const FormItem = Form.Item
const {RangePicker} = DatePicker
const TYPE = ['23', '24', '25', '55'] // s06 s07 s08 s11 商超销售配送 商超销售直送 商超销售直通 商超销售电商

@connect(
  state => ({...state.outWarehouseListWave}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super()
    this.columns = [{
      title: '出库单号',
      width: 150,
      dataIndex: 'orderCode',
      key: 'orderCode'
    }, {
      title: '业务类型',
      width: 100,
      dataIndex: 'businessType',
      key: 'businessType'
    },
    // {
    //   title: '补单空出',
    //   width: 100,
    //   dataIndex: 'noSendFlagDesc',
    //   key: 'noSendFlagDesc'
    // },
    {
      title: '门店',
      width: 150,
      dataIndex: 'shopCode',
      key: 'shopCode',
      render: (text, record, index) => `${record.shopCode || ''} - ${record.shopName || ''}`
    }, {
      title: '客户',
      width: 150,
      dataIndex: 'customerCode',
      key: 'customerCode',
      render: (text, record, index) => `${record.customerCode || ''} - ${record.customerName || ''}`
    }, {
      title: '客户地址',
      width: 150,
      dataIndex: 'subCustomerAddress',
      key: 'subCustomerAddress'
    }, {
      title: '站点',
      width: 150,
      dataIndex: 'stationCode',
      key: 'stationCode',
      render: (text, record, index) => `${record.stationCode || ''} - ${record.stationName || ''}`
    }, {
      title: '创建时间',
      width: 150,
      dataIndex: 'createTime',
      key: 'createTime'
    }]
  }
  componentDidMount() {
  }

  // 生成波次
  handleSubmit = (e) => {
    const {form, handleCreate, selectedRowKeysOM} = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        if (selectedRowKeysOM.length == 0) {
          message.error('请选择出库单')
          return
        }
        let data = {
          businessType: values.businessType.value,
          shopType: (values.shopType && values.shopType.value) || '',
          shopTypeDesc: (values.shopType && values.shopType.children) || '',
          shippedOrderRequestList: []
        }
        // 判断是否是BBC集波
        if (!(values.businessType && BBC.indexOf(values.businessType.value) != -1)) {
          data.planSendDate = moment(values.initDate2).format('YYYY-MM-DD')
        }
        selectedRowKeysOM.map((o) => {
          data.shippedOrderRequestList.push({
            id: o
          })
        })
        handleCreate({data}, () => {
          message.success('操作成功')
          const {handleGetList, searchParams, page, size} = this.props
          handleGetList({searchParams: {page, size, ...searchParams}})
          this.handleCloseModal()
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowModal: false})
  }

    // 重置
  reset = () => {
    const {form} = this.props
    form.resetFields()
    // console.log(form, 1)
    // let inputObj = form.getFieldsValue()
    // update({searchParams: {}})
  }

  // 查询
  query = (isBBC) => {
    console.log('isBBC is', isBBC)
    const {handleGetListOM, form} = this.props
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        if (isBBC) {
          let reqObj = {...values}
          console.log('reqObj is', reqObj)
          if (reqObj.initDate2) {
            reqObj.planSendDate = moment(reqObj.initDate2).format('YYYY-MM-DD')
          }
          delete reqObj.initDate2
          for (let i in reqObj) {
            if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
              reqObj[i] = reqObj[i].value
            }
          }

          if (reqObj.initDate && reqObj.initDate.length) {
            reqObj.createTimeStart = moment(reqObj.initDate[0]).format('YYYY-MM-DD')
            reqObj.createTimeEnd = moment(reqObj.initDate[1]).format('YYYY-MM-DD')
          }

          console.log('reqObj is 111', reqObj)
          handleGetListOM({searchParams: {...reqObj}})
        } else {
          let reqObj = {...values}
          if (reqObj.initDate2) {
            reqObj.planSendDate = moment(reqObj.initDate2).format('YYYY-MM-DD')
          }
          delete reqObj.initDate2
          for (let i in reqObj) {
            if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
              reqObj[i] = reqObj[i].value
            }
          }

          if (reqObj.initDate && reqObj.initDate.length) {
            reqObj.createTimeStart = moment(reqObj.initDate[0]).format('YYYY-MM-DD')
            reqObj.createTimeEnd = moment(reqObj.initDate[1]).format('YYYY-MM-DD')
          }
          delete reqObj.initDate
          handleGetListOM({searchParams: {...reqObj}})
        }
      }
    })
  }

  handleSelectChange = () => {
    const {form} = this.props
    let inputObj = form.getFieldsValue()
    let reqObj = {...inputObj}
    console.log(' reqObj is', reqObj)
    let _this = this
    setTimeout(() => {
      _this.query(reqObj.businessType && BBC.indexOf(reqObj.businessType.value) != -1)
    }, 1000)
  }
  render() {
    const {form: {getFieldDecorator}, modalTuttonLoading, modalData, searchResultRecord, deliveryTableLoading, selectedRowKeysOM, selectedRowsOM, form} = this.props
    const formItemLayout = {
      labelCol: {
        xs: {span: 24},
        sm: {span: 8}
      },
      wrapperCol: {
        xs: {span: 24},
        sm: {span: 16}
      }
    }
    let rowSelection = {
      selectedRowKeysOM,
      onChange: (selectedRowKeys, selectedRows) => {
        const {update} = this.props
        update({selectedRowKeysOM: selectedRowKeys, selectedRowsOM, selectedRows})
      },
      selectedRowKeys: selectedRowKeysOM
    }
    let inputObj = form.getFieldsValue()
    let reqObj = {...inputObj}
    return (
      <Modal
        title={'创建波次'}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        width={980}
        footer={null}
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <Row gutter={16}>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="业务类型"
              >
                {getFieldDecorator('businessType', {
                  initialValue: modalData.businessType,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_WAVES_BUSINESSKEYVALUES}
                    selectItem={{key: 'key', valueKey: 'value'}}
                    handleSelectChange={this.handleSelectChange}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="订单号"
              >
                {getFieldDecorator('orderCode', {
                  initialValue: modalData.orderCode
                })(
                  <Input placeholder='批量查询,请用逗号隔开' />
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="状态"
              >
                {getFieldDecorator('status', {
                  initialValue: modalData.status
                })(
                  <CsxFormSelect
                    dateApi={API_WAVES_STATUSKEYVALUES}
                    selectItem={{key: 'key', valueKey: 'value'}}
                  />
                )}
              </FormItem>
            </Col>
            {
              (reqObj.businessType && TYPE.indexOf(reqObj.businessType.value) != -1) ? <Col span={8}>
               <FormItem
                 {...formItemLayout}
                 label="门店类型"
               >
                 {getFieldDecorator('shopType', {
                   initialValue: modalData.shopType,
                   rules: [{
                     required: true, message: '请选择!'
                   }]
                 })(
                   <CsxFormSelect
                     dateApi={API_WAVES_SHOPTYPEKEYVALUES}
                     selectItem={{key: 'key', valueKey: 'value'}}
                   />
                 )}
               </FormItem>
             </Col> : <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="门店类型"
              >
                {getFieldDecorator('shopType', {
                  initialValue: modalData.shopType,
                  rules: [{
                    required: false
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_WAVES_SHOPTYPEKEYVALUES}
                    selectItem={{key: 'key', valueKey: 'value'}}
                  />
                )}
              </FormItem>
            </Col>
            }
            {/* <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="补单空出"
              >
                {getFieldDecorator('noSendFlag', {
                  initialValue: modalData.noSendFlag
                })(
                  <CsxFormSelect
                  data={NOSENDFLAG}
                    selectItem={{key: 'key', valueKey: 'value'}}
                  />
                )}
              </FormItem>
            </Col> */}
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="客户"
              >
                {getFieldDecorator('customerCode', {
                  initialValue: modalData.customerCode
                })(
                  <Input placeholder='批量查询,请用逗号隔开'/>
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="站点"
              >
                {getFieldDecorator('stationCode', {
                  initialValue: modalData.stationCode
                })(
                  <Input />
                )}
              </FormItem>
            </Col>

            {
              // (reqObj.businessType && BBC.indexOf(reqObj.businessType.value) != -1) ? <Col span={8}>
              //   <FormItem
              //     labelCol = {{
              //       xs: {span: 24},
              //       sm: {span: 8}
              //     }}
              //     wrapperCol = {{
              //       xs: {span: 24},
              //       sm: {span: 16}
              //     }}
              //     label="计划发货日期"
              //   >
              //     {getFieldDecorator('initDate2', {
              //       initialValue: modalData.initDate2,
              //       rules: [{
              //         required: false
              //       }]
              //     })(
              //       <DatePicker
              //         style={{
              //           width: '200px'
              //         }}
              //         format={'YYYY-MM-DD'}
              //       />
              //     )}
              //   </FormItem>
              // </Col>
              //  :
              <Col span={8}>
                <FormItem
                  labelCol = {{
                    xs: {span: 24},
                    sm: {span: 8}
                  }}
                  wrapperCol = {{
                    xs: {span: 24},
                    sm: {span: 16}
                  }}
                  label="计划发货日期"
                >
                  {getFieldDecorator('initDate2', {
                    initialValue: modalData.initDate2,
                    rules: [{
                      required: true, message: '请选择!'
                    }]
                  })(
                    <DatePicker
                      style={{
                        width: '200px'
                      }}
                      format={'YYYY-MM-DD'}
                    />
                  )}
                </FormItem>
              </Col>
            }
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="门店"
              >
                {getFieldDecorator('shopCode', {
                  initialValue: modalData.shopCode
                })(
                  // <CsxFormSelect
                  //   searchParams={{merchantType: form.getFieldsValue().shopType && form.getFieldsValue().shopType.value}}
                  //   errMessage="请先选择门店类型"
                  //   dateApi={API_WAVES_SHOPKEYVALUES}
                  //   selectItem={{key: 'code', valueKey: ['code', 'name']}}
                  // />
                  <CsxFormMultipleSelect
                    searchParams={{merchantType: form.getFieldsValue().shopType && form.getFieldsValue().shopType.value}}
                    errMessage="请先选择门店类型"
                    dateApi={API_WAVES_SHOPKEYVALUES}
                    noReady={form.getFieldsValue().shopType ? false : true}
                    selectItem={{key: 'code', valueKey: ['code', 'name']}}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                labelCol = {{
                  xs: {span: 24},
                  sm: {span: 8}
                }}
                wrapperCol = {{
                  xs: {span: 24},
                  sm: {span: 16}
                }}
                label="创建时间"
              >
                {getFieldDecorator('initDate', {
                  initialValue: modalData.initDate
                })(
                  <RangePicker
                    format={'YYYY-MM-DD'}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={24}>
              <Row type="flex" justify="end" >
                <Button type="primary" style={{marginRight: '18px'}} onClick={this.query.bind(this, (reqObj.businessType && BBC.indexOf(reqObj.businessType.value) != -1))}>查询</Button>
                <Button type="primary" style={{marginRight: '18px'}} onClick={this.reset} >重置</Button>
                <Button type="primary" htmlType="submit" loading={modalTuttonLoading}>生成波次</Button>
              </Row>
            </Col>
          </Row>
          <p style={{textAlign: 'right'}}>查询结果：{searchResultRecord.length}</p>
          <div className='table-group' style={{margin: 0}}>
            <Table
              rowKey={record => record.id}
              loading={deliveryTableLoading}
              columns={this.columns}
              scroll={{x: 'max-content', y: 400}}
              dataSource={searchResultRecord}
              pagination={false}
              rowSelection={rowSelection}
            />
          </div>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



