import React, {PureComponent} from 'react'
import {
  Modal,
  Table,
  Button,
  message
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {getNowPageId} from '../../global/components/menuData'

@connect(
  state => ({...state.outWarehouseListWave}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {

    }
    this.columns = [{
      title: '商品编号',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品名称',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '商品类型',
      width: 100,
      dataIndex: 'productType',
      key: 'productType'
    }, {
      title: '子品/母品编号',
      width: 150,
      dataIndex: 'linkProductCode',
      key: 'linkProductCode'
    }, {
      title: '采购组',
      width: 100,
      dataIndex: 'purchaseGroup',
      key: 'purchaseGroup'
    }, {
      title: '缺货总量',
      width: 100,
      dataIndex: 'shortageTotal',
      key: 'shortageTotal'
    }, {
      title: '单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '订单编号',
      width: 150,
      dataIndex: 'outCode',
      key: 'outCode'
    }, {
      title: '配送地址',
      width: 150,
      dataIndex: 'deliveryAddress',
      key: 'deliveryAddress'
    }, {
      title: '缺货量',
      width: 100,
      dataIndex: 'shortageQty',
      key: 'shortageQty'
    }, {
      title: '是否支持地采',
      width: 100,
      dataIndex: 'localPurchaseFlag',
      key: 'localPurchaseFlag',
      render: (text, record, index) => <span>{record.localPurchaseFlag ? '是' : '否'}</span>
    }]
  }
  componentDidMount() {
    this._getAsyncData()
    const {GetButtonPower} = this.props
    const MEUN = JSON.parse(localStorage.getItem('b2bUserMenu'))
    const ID = getNowPageId(MEUN, window.location.pathname)
    GetButtonPower({urlParams: [ID]})
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    const {handleGetStockOut, modalId} = this.props
    handleGetStockOut({searchParams: {waveCode: modalId}})
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowStockoutModal: false})
  }
  // 导出
  handleExport = () => {
    const {handleSocktOutExport, searchParams, page, size, modalId} = this.props
    handleSocktOutExport({searchParams: {page, size, waveCode: modalId.toString(), ...searchParams}}, () => {
      message.success('导出成功')
    })
  }
  //申报地采
  deClar=() => {
    const {selectedRowssStock, handleDeClar, update} = this.props
    let array = []
    if (!selectedRowssStock.length) {
      message.error('请先选择要进行申请地采的商品')
      return
    }
    selectedRowssStock.map(o => array.push({id: o.id}))
    console.log(array, 222)
    handleDeClar({data: array}, () => {
      message.success('申报地采成功')
      this._getAsyncData()
    })
  }
  render() {
    const {deliveryTableLoading, searchResultRecord, selectedRowKeysStock, outModalData, tableActiveKey, applyForGroundMining} = this.props
    console.log(selectedRowKeysStock, 333)
    const rowSelections = {
      selectedRowKeys: selectedRowKeysStock,
      onChange: (selectedRowKeys, selectedRows) => {
        const {update} = this.props
        update({selectedRowKeysStock: selectedRowKeys, selectedRowssStock: selectedRows})
      }
    }
    return (
        <Modal
          title="缺货明细"
          visible={true}
          onCancel={this.handleCloseModal}
          footer={null}
          mask={false}
          width={1100}
        >
          <Button type="primary" onClick={this.handleExport}>导出缺货明细</Button>
          {
            applyForGroundMining && outModalData.businessType == 18 && tableActiveKey == 1 ?
            <Button type="primary" style={{marginLeft: '10px'}} onClick={this.deClar}>申报地采</Button> : ''
          }

          <div className='table-group' style={{margin: 0}}>
            <Table
              rowKey={(record, index) => index}
              loading={deliveryTableLoading}
              columns={this.columns}
              scroll={{x: 'max-content', y: 400}}
              dataSource={searchResultRecord}
              rowSelection={rowSelections}
              pagination={false}
            />
          </div>
        </Modal>
    )
  }
}



